﻿@mixin viewbuttons {
	display: inline-block;
	background: #9addfc;
	border: 0;
	outline: 0;
	border-radius: 5px;
	color: #335477;
	text-align: center;
	text-transform: uppercase;
	min-width: 230px;
	font-size: 18px;
	line-height: 25px;
	font-family: $font-semibold;
	padding: 20px 15px;

	&:hover,
	&focus {
		opacity: 0.7;
		transition: .4s ease;
	}
}
