@font-face {
  font-family: "OpenSans-Regular";
  src: url("../fonts/OpenSans/open-sans.regular.ttf") format("truetype"); }

@font-face {
  font-family: "OpenSans-ExtraBold";
  src: url("../fonts/OpenSans/open-sans.extrabold.ttf") format("truetype"); }

@font-face {
  font-family: "OpenSans-Bold";
  src: url("../fonts/OpenSans/open-sans.bold.ttf") format("truetype"); }

@font-face {
  font-family: "OpenSans-SemiBold";
  src: url("../fonts/OpenSans/open-sans.semibold.ttf") format("truetype"); }

body {
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  background: #fff;
  color: #fff;
  font-family: 'OpenSans-Regular','OpenSans-ExtraBold','OpenSans-Bold','OpenSans-SemiBold', sans-serif;
  font-size: 14px;
  line-height: auto;
  -webkit-text-size-adjust: 100%; }

.container {
  width: 1230px;
  margin: 0 auto;
  max-width: 100%;
  overflow-x: hidden; }

.page_header {
  background: #0e88f3;
  min-height: 86px;
  width: 100%; }

.page_header_top {
  position: relative;
  display: flex;
  align-content: stretch; }
  .page_header_top .page_header_top_logo {
    text-decoration: none;
    margin-top: 30px;
    margin-right: 0px; }
    .page_header_top .page_header_top_logo img {
      width: 175px;
      height: 27px; }

.page_header_top_nav {
  list-style-type: none;
  color: #fff;
  font-family: "OpenSans-Regular", sans-serif;
  overflow: hidden; }
  .page_header_top_nav li {
    display: inline-block;
    cursor: pointer;
    margin-top: 25px; }
    .page_header_top_nav li a {
      text-decoration: none;
      color: #d2e7fb;
      padding: 10px;
      font-size: 14px;
      line-height: 20px; }
      .page_header_top_nav li a:hover, .page_header_top_nav li a:focus {
        color: #fff; }

/* BURGER MENU ON TOGGLE TRANSFORM CLASSES */
.page_header_top_burger_menu {
  display: none;
  text-align: center;
  position: relative;
  cursor: pointer; }

.burger_bars {
  display: inline-block;
  width: auto;
  margin: 0 auto;
  -webkit-transition: all .7s ease;
  -moz-transition: all .7s ease;
  -ms-transition: all .7s ease;
  -o-transition: all .7s ease;
  transition: all .7s ease; }

.middle {
  margin: 0 auto; }

.bar {
  display: block;
  height: 3px;
  width: 40px;
  background: #fff;
  margin: 8px auto; }

.transformFirstBar {
  -webkit-transform: translateY(8px) rotateZ(45deg);
  -moz-transform: translateY(8px) rotateZ(45deg);
  -ms-transform: translateY(8px) rotateZ(45deg);
  -o-transform: translateY(8px) rotateZ(45deg);
  transform: translateY(8px) rotateZ(45deg);
  -webkit-transition: all .7s ease;
  -moz-transition: all .7s ease;
  -ms-transition: all .7s ease;
  -o-transition: all .7s ease;
  transition: all .7s ease;
  background-color: #d2e7fb; }

.transformSecondBar {
  width: 0;
  background-color: #d2e7fb;
  -webkit-transition: all .7s ease;
  -moz-transition: all .7s ease;
  -ms-transition: all .7s ease;
  -o-transition: all .7s ease;
  transition: all .7s ease; }

.transformThirdBar {
  -webkit-transform: translateY(-15px) rotateZ(-45deg);
  -moz-transform: translateY(-15px) rotateZ(-45deg);
  -ms-transform: translateY(-15px) rotateZ(-45deg);
  -o-transform: translateY(-15px) rotateZ(-45deg);
  transform: translateY(-15px) rotateZ(-45deg);
  -webkit-transition: all .7s ease;
  -moz-transition: all .7s ease;
  -ms-transition: all .7s ease;
  -o-transition: all .7s ease;
  transition: all .7s ease;
  background-color: #d2e7fb; }

@media screen and (max-width: 992px) {
  .container {
    width: 100%;
    margin: 0 auto;
    max-width: 100%;
    overflow-x: hidden; }
  .page_header {
    min-height: 60px; }
  .page_header_top {
    justify-content: space-between;
    flex-wrap: wrap; }
    .page_header_top .page_header_top_logo {
      padding-left: 15px;
      margin-top: 20px; }
      .page_header_top .page_header_top_logo img {
        width: 100px;
        height: 20px; }
  .page_header_top_burger_menu {
    display: block;
    padding-right: 8px;
    margin-top: 8px; }
  .page_header_top_nav {
    display: none;
    width: 100%;
    margin-left: -40px; }
    .page_header_top_nav li {
      display: block;
      padding-bottom: 20px;
      border-bottom: 2px solid #d2e7fb; }
      .page_header_top_nav li a {
        text-transform: uppercase;
        font-family: "OpenSans-Bold", sans-serif; }
    .page_header_top_nav li:last-of-type {
      padding-bottom: 10px;
      border-bottom: none; } }

@media screen and (min-width: 993px) and (max-width: 1440px) {
  .page_header_top {
    justify-content: space-around; } }

.hero_screen {
  display: inline-block;
  position: relative;
  background: #0e88f3;
  width: 100%; }

.hero_screen_info {
  display: flex;
  align-content: stretch;
  flex-wrap: wrap;
  margin-bottom: 80px; }
  .hero_screen_info .hero_screen_info_details {
    margin-right: 40px;
    order: 1; }
  .hero_screen_info .hero_screen_info_image {
    order: 2; }

.hero_screen_info_details_title {
  font-family: "OpenSans-Regular", sans-serif;
  font-size: 36px;
  line-height: 43px;
  white-space: pre-line;
  display: block;
  margin-bottom: 0px; }

.hero_screen_info_details_tip {
  white-space: pre-line;
  font-size: 14px;
  line-height: 25px;
  font-family: "OpenSans-Regular", sans-serif;
  color: #d2e7fb;
  margin-bottom: 40px;
  display: block; }

.view_button {
  display: inline-block;
  background: #9addfc;
  border: 0;
  outline: 0;
  border-radius: 5px;
  color: #335477;
  text-align: center;
  text-transform: uppercase;
  min-width: 230px;
  font-size: 18px;
  line-height: 25px;
  font-family: "OpenSans-SemiBold", sans-serif;
  padding: 20px 15px; }
  .view_button:hover, .view_buttonfocus {
    opacity: 0.7;
    transition: .4s ease; }

.hero_screen_details_video {
  display: block;
  font-family: "OpenSans-Regular", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-decoration: underline;
  color: #d2e7fb;
  margin-top: 20px;
  margin-left: 38px;
  position: relative; }

.hero_screen_details_video::before {
  content: '';
  left: calc(28% + 10px);
  top: 4px;
  position: absolute;
  background: url(../img/ui/play_icon.png) no-repeat center;
  width: 7px;
  height: 9px; }

.hero_screen_details_video::after {
  content: '';
  left: calc(26.7% + 10px);
  top: 0px;
  position: absolute;
  background: url(../img/ui/ellipse_icon.png) no-repeat center;
  width: 18px;
  height: 18px; }

.hero_screen_info_image img {
  width: 562px;
  height: 432px;
  max-width: 100%; }

.hero_screen_info_retailers {
  display: flex;
  align-items: center;
  justify-content: stretch;
  margin-left: 128px;
  margin-top: 63px;
  order: 3; }
  .hero_screen_info_retailers span {
    font-size: 14px;
    line-height: 20px;
    font-family: "OpenSans-Regular", sans-serif;
    color: #d2e7fb;
    white-space: nowrap;
    margin-right: 30px; }

.hero_screen_info_retailers a {
  margin-right: 25px;
  text-decoration: none; }
  .hero_screen_info_retailers a img {
    max-width: 100%;
    filter: brightness(0) invert(1);
    opacity: 0.6; }
    .hero_screen_info_retailers a img:hover, .hero_screen_info_retailers a img:focus {
      filter: none;
      opacity: 1; }

@media screen and (max-width: 992px) {
  .hero_screen_info {
    flex-direction: column;
    text-align: center;
    margin-bottom: 80px; }
    .hero_screen_info .hero_screen_info_details {
      margin-right: 0px;
      order: 1; }
    .hero_screen_info .hero_screen_info_image {
      order: 2;
      margin-top: 40px; }
  .hero_screen_info_details_title,
  .hero_screen_info_details_tip {
    white-space: normal;
    margin-bottom: 40px; }
  .view_button {
    display: block;
    margin: auto; }
  .hero_screen_details_video {
    display: inline-block;
    margin-left: 0; }
  .hero_screen_details_video::before {
    content: '';
    left: calc(105% + 10px); }
  .hero_screen_details_video::after {
    content: '';
    left: calc(100% + 10px); }
  .hero_screen_info_image img {
    width: 300px;
    height: 231px;
    max-width: 100%; }
  .hero_screen_info_retailers {
    margin-left: 0;
    flex-direction: column; }
    .hero_screen_info_retailers span {
      margin-right: 0; }
  .hero_screen_info_retailers a {
    margin-right: 0;
    margin-top: 30px; } }

@media screen and (min-width: 993px) and (max-width: 1440px) {
  .hero_screen_info {
    margin-bottom: 80px;
    margin-top: 60px;
    justify-content: space-around; }
  .hero_screen_info_image img {
    width: 300px;
    height: 231px;
    max-width: 100%; }
  .hero_screen_info_retailers {
    margin-left: 0; } }
