﻿.hero_screen {
	display: inline-block;
	position: relative;
	background: $Bg-color-blue;
	width: 100%;
}

.hero_screen_info {
	display: flex;
	align-content: stretch;
	flex-wrap: wrap;
	margin-bottom: 80px;

	.hero_screen_info_details {
		margin-right: 40px;
		order: 1;
	}

	.hero_screen_info_image {
		order: 2;
	}
}

.hero_screen_info_details_title {
	font-family: $font-regular;
	font-size: 36px;
	line-height: 43px;
	white-space: pre-line;
	display: block;
	margin-bottom: 0px;
}

.hero_screen_info_details_tip {
	white-space: pre-line;
	font-size: 14px;
	line-height: 25px;
	font-family: $font-regular;
	color: $description-text;
	margin-bottom: 40px;
	display: block;
}

.view_button {
	@include viewbuttons;
}

.hero_screen_details_video {
	display: block;
	font-family: $font-regular;
	font-size: 14px;
	line-height: 20px;
	text-decoration: underline;
	color: $description-text;
	margin-top: 20px;
	margin-left: 38px;
	position: relative;
}

.hero_screen_details_video::before {
	content: '';
	left: calc(28% + 10px);
	top: 4px;
	position: absolute;
	background: url(../img/ui/play_icon.png) no-repeat center;
	width: 7px;
	height: 9px;
}

.hero_screen_details_video::after {
	content: '';
	left: calc(26.7% + 10px);
	top: 0px;
	position: absolute;
	background: url(../img/ui/ellipse_icon.png) no-repeat center;
	width: 18px;
	height: 18px;
}

.hero_screen_info_image img {
	width: 562px;
	height: 432px;
	max-width: 100%;
}

.hero_screen_info_retailers {
	display: flex;
	align-items: center;
	justify-content: stretch;
	margin-left: 128px;
	margin-top: 63px;
	order: 3;

	span {
		font-size: 14px;
		line-height: 20px;
		font-family: $font-regular;
		color: $description-text;
		white-space: nowrap;
		margin-right: 30px;
	}
}

.hero_screen_info_retailers a {
	margin-right: 25px;
	text-decoration: none;

	img {
		max-width: 100%;
		filter: brightness(0) invert(1);
		opacity: 0.6;

		&:hover,
		&:focus {
			filter: none;
			opacity: 1;
		}
	}
}

// MEDIA QUERIES

@media screen and (max-width: 992px) {
	.hero_screen_info {
		flex-direction: column;
		text-align: center;
		margin-bottom: 80px;

		.hero_screen_info_details {
			margin-right: 0px;
			order: 1;
		}

		.hero_screen_info_image {
			order: 2;
			margin-top: 40px;
		}
	}

	.hero_screen_info_details_title,
	.hero_screen_info_details_tip {
		white-space: normal;
		margin-bottom: 40px;
	}

	.view_button {
		display: block;
		margin: auto;
	}

	.hero_screen_details_video {
		display: inline-block;
		margin-left: 0;
	}

	.hero_screen_details_video::before {
		content: '';
		left: calc(105% + 10px);
	}

	.hero_screen_details_video::after {
		content: '';
		left: calc(100% + 10px);
	}

	.hero_screen_info_image img {
		width: 300px;
		height: 231px;
		max-width: 100%;
	}

	.hero_screen_info_retailers {
		margin-left: 0;
		flex-direction: column;

		span {
			margin-right: 0;
		}
	}

	.hero_screen_info_retailers a {
		margin-right: 0;
		margin-top: 30px;
	}
}

@media screen and (min-width: 993px) and (max-width: 1440px) {
	.hero_screen_info {
		margin-bottom: 80px;
		margin-top: 60px;
		justify-content: space-around;
	}

	.hero_screen_info_image img {
		width: 300px;
		height: 231px;
		max-width: 100%;
	}

	.hero_screen_info_retailers {
		margin-left: 0;
	}
}
