@font-face {
	font-family: "OpenSans-Regular";
	src: url("../fonts/OpenSans/open-sans.regular.ttf") format("truetype");
}

@font-face {
	font-family: "OpenSans-ExtraBold";
	src: url("../fonts/OpenSans/open-sans.extrabold.ttf") format("truetype");
}

@font-face {
	font-family: "OpenSans-Bold";
	src: url("../fonts/OpenSans/open-sans.bold.ttf") format("truetype");
}

@font-face {
	font-family: "OpenSans-SemiBold";
	src: url("../fonts/OpenSans/open-sans.semibold.ttf") format("truetype");
}

body {
	width: 100%;
	overflow-x: hidden;
	margin: 0;
	padding: 0;
	background: #fff;
	color: #fff;
	font-family: 'OpenSans-Regular','OpenSans-ExtraBold','OpenSans-Bold','OpenSans-SemiBold', sans-serif;
	font-size: 14px;
	line-height: auto;
	-webkit-text-size-adjust: 100%;
}

.container {
	width: 1230px;
	margin: 0 auto;
	max-width: 100%;
	overflow-x: hidden;
}

// SCSS VARIABLES

$font-regular: "OpenSans-Regular", sans-serif;
$font-bold: "OpenSans-Bold", sans-serif;
$font-extrabold: "OpenSans-ExtraBold", sans-serif;
$font-semibold: "OpenSans-SemiBold", sans-serif;
$Bg-color-blue: #0e88f3;
$description-text: #d2e7fb;

.page_header {
	background: $Bg-color-blue;
	min-height: 86px;
	width: 100%;
}

.page_header_top {
	position: relative;
	display: flex;
	align-content: stretch;

	.page_header_top_logo {
		text-decoration: none;
		margin-top: 30px;
		margin-right: 0px;

		img {
			width: 175px;
			height: 27px;
		}
	}
}

.page_header_top_nav {
	list-style-type: none;
	color: #fff;
	font-family: $font-regular;
	overflow: hidden;

	li {
		display: inline-block;
		cursor: pointer;
		margin-top: 25px;

		a {
			text-decoration: none;
			color: $description-text;
			padding: 10px;
			font-size: 14px;
			line-height: 20px;

			&:hover,
			&:focus {
				color: #fff;
			}
		}
	}
}

/* BURGER MENU ON TOGGLE TRANSFORM CLASSES */

.page_header_top_burger_menu {
	display: none;
	text-align: center;
	position: relative;
	cursor: pointer;
}

.burger_bars {
	display: inline-block;
	width: auto;
	margin: 0 auto;
	-webkit-transition: all .7s ease;
	-moz-transition: all .7s ease;
	-ms-transition: all .7s ease;
	-o-transition: all .7s ease;
	transition: all .7s ease;
}

.middle {
	margin: 0 auto;
}

.bar {
	display: block;
	height: 3px;
	width: 40px;
	background: #fff;
	margin: 8px auto;
}

.transformFirstBar {
	-webkit-transform: translateY(8px) rotateZ(45deg);
	-moz-transform: translateY(8px) rotateZ(45deg);
	-ms-transform: translateY(8px) rotateZ(45deg);
	-o-transform: translateY(8px) rotateZ(45deg);
	transform: translateY(8px) rotateZ(45deg);
	-webkit-transition: all .7s ease;
	-moz-transition: all .7s ease;
	-ms-transition: all .7s ease;
	-o-transition: all .7s ease;
	transition: all .7s ease;
	background-color: $description-text;
}

.transformSecondBar {
	width: 0;
	background-color: $description-text;
	-webkit-transition: all .7s ease;
	-moz-transition: all .7s ease;
	-ms-transition: all .7s ease;
	-o-transition: all .7s ease;
	transition: all .7s ease;
}

.transformThirdBar {
	-webkit-transform: translateY(-15px) rotateZ(-45deg);
	-moz-transform: translateY(-15px) rotateZ(-45deg);
	-ms-transform: translateY(-15px) rotateZ(-45deg);
	-o-transform: translateY(-15px) rotateZ(-45deg);
	transform: translateY(-15px) rotateZ(-45deg);
	-webkit-transition: all .7s ease;
	-moz-transition: all .7s ease;
	-ms-transition: all .7s ease;
	-o-transition: all .7s ease;
	transition: all .7s ease;
	background-color: $description-text;
}

// MEDIA QUERIES

@media screen and (max-width: 992px) {
	.container {
		width: 100%;
		margin: 0 auto;
		max-width: 100%;
		overflow-x: hidden;
	}

	.page_header {
		min-height: 60px;
	}

	.page_header_top {
		justify-content: space-between;
		flex-wrap: wrap;

		.page_header_top_logo {
			padding-left: 15px;
			margin-top: 20px;

			img {
				width: 100px;
				height: 20px;
			}
		}
	}

	.page_header_top_burger_menu {
		display: block;
		padding-right: 8px;
		margin-top: 8px;
	}

	.page_header_top_nav {
		display: none;
		width: 100%;
		margin-left: -40px;

		li {
			display: block;
			padding-bottom: 20px;
			border-bottom: 2px solid $description-text;

			a {
				text-transform: uppercase;
				font-family: $font-bold;
			}
		}

		li:last-of-type {
			padding-bottom: 10px;
			border-bottom: none;
		}
	}
}

@media screen and (min-width: 993px) and (max-width: 1440px) {
	.page_header_top {
		justify-content: space-around;
	}
}
